.cat {
  --gap: calc(100% / 11);
  position: relative;
  aspect-ratio: 1 / 1;
  background: center / cover no-repeat;
  overflow: hidden;
  transition: transform 0.5s;
}

.cat:hover {
  cursor: pointer;
  box-shadow: var(--box-shadow-card);
  transform: scale(1.138);
  transition: transform 0.5s, box-shadow 0.5s;
}

.cat__overlay {
  position: absolute;
  left: 0;
  bottom: 0;
  visibility: hidden;
  opacity: 0;
  width: 100%;
  height: 37%;
  background: var(--background-gradient-card);
  transition: all 0.5s;
}

.cat:hover .cat__overlay {
  visibility: visible;
  opacity: 1;
  transition: all 0.5s;
}

.cat__button-like {
  position: absolute;
  padding: var(--gap);
  right: var(--gap);
  bottom: calc(var(--gap) * 2);
  background: no-repeat url(../images/favorite_not-active.svg) center / cover;
  opacity: 1;
}

.cat__button-like_active {
  background-image: url(../images/favorite_active.svg);
}

.cat__button-like:hover {
  opacity: 1;
  background-image: url(../images/favorite_hovered.svg);
}

.cat__button-like:active {
  background-image: url(../images/favorite_active.svg);
}
